<template>
  <div
    class="modal fade exportServicesModal show modal-overlay"
    style="display: block"
    tabindex="-1"
    @click.self="$emit('resetModal', '')"
  >
    <the-modal :modal-id="'exportMeetModal'">
      <template #header>
        <img src="@/assets/images/ic_meet-on.png" alt="teams" />
        <button
          type="button"
          class="close"
          data-dismiss="modal"
          aria-label="Close"
          @click="$emit('resetModal', '')"
        >
          <span aria-hidden="true">&times;</span>
        </button>
      </template>

      <p>
        <!-- Google Meet<br />
        URL -->
        {{ this.data.gmeet1 }}
      </p>
      <form>
        <div class="form-group row">
          <label for="meetText" class="col-sm-12 col-form-label text-center"
            >
            {{ this.data.gmeet2 }}
            <!-- URL -->
            </label
          >
          <div class="col-sm-12">
            <input
              type="text"
              class="form-control"
              id="meetText"
              v-model="teamsId"
              placeholder="XXXXXX"
            />
          </div>
        </div>
        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            name="agree"
            id="agree"
            v-model="isChecked"
          />
          <label class="form-check-label" for="agree">
            
            {{ this.data.gmeet3 }}
            <!-- AI<a href="#"></a> -->
          </label>
        </div>
      </form>

      <div style="display: flex;margin: auto;">
        <button
          type="button"
          class="btn btn-primary"
          data-dismiss="modal"
          data-toggle="modal"
          data-target="#recording"
          @click="handle"
        >
          {{ this.data.gmeet4 }}
          <!-- Google Meet -->
        </button>
      </div>
    </the-modal>
  </div>
</template>

<script>
import TheModal from "./the-modal.vue";

export default {
  components: {
    TheModal,
  },
  props: {
    data:{
    },
  },
  data: () => ({
    teamsId: "",
    password: "",
    isChecked: false,
  }),
  methods: {
    handle() {
      if (!this.isChecked) return;
      if (this.teamsId == "") return;

      window.open(this.teamsId, "_blank");
      this.$emit("resetModal", "");
    }
  }
};
</script>
